// Fonts
@font-face {
  font-family: 'Lato';
  src:  url('/fonts/Lato/LatoLatin-Regular.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-Regular.woff') format('woff');
}

@font-face {
  font-family: 'Lato Bold';
  src:  url('/fonts/Lato/LatoLatin-Bold.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-Bold.woff') format('woff');
}

@font-face {
  font-family: 'Lato Bold Italic';
  src:  url('/fonts/Lato/LatoLatin-BoldItalic.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-BoldItalic.woff') format('woff');
}

@font-face {
  font-family: 'Lato Italic';
  src:  url('/fonts/Lato/LatoLatin-Italic.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-Italic.woff') format('woff');
}

@font-face {
  font-family: 'Lato Light';
  font-weight: 300;
  src:  url('/fonts/Lato/LatoLatin-Light.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-Light.woff') format('woff');
}

@font-face {
  font-family: 'Lato Semibold';
  src:  url('/fonts/Lato/LatoLatin-Semibold.woff2') format('woff2'),
        url('/fonts/Lato/LatoLatin-Semibold.woff') format('woff');
}

// Global styles
html {
  height: 100%;
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  text-rendering: optimizeLegibility;

  > * {
    width: 100%;
  }
}

h2 {
  margin: 4rem 0 2rem;
  font-size: 2.2rem;
  font-family: 'Lato Bold', sans-serif;
  text-align: center;
}

h3 {
  margin: 3rem 0 0.5rem;
  font-family: 'Lato Bold', sans-serif;
}

p {
  a {
    font-family: 'Lato Bold', sans-serif;
    color: #ff0055;

    &:hover {
      text-decoration: none;
    }
  }
}

a.primary-action,
li.primary-action a {
  display: block;
  max-width: 30rem;
  margin: auto;
  padding: 1.6rem 2rem 1.8rem;
  font-family: 'Lato Semibold', sans-serif;
  font-size: 16px;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  text-shadow: 0 0.1rem 0.2rem #80192B;
  background-image: linear-gradient(-180deg, #FF3377 0%, #D92B48 100%);
  border-radius: 0.4rem;
}

// Override default width/height caused by alt attributes in unloaded images
img[data-src]:not(.loaded) {
  width: 0;
  height: 0;
}

// Main header
body {
  > header {
    display: flex;
    flex-direction: column;
    flex: 0 0 auto;
    padding: 0.8rem 0;
    font-size: 1.8rem;
    background-color: #fafafa;
    background-image: linear-gradient(-180deg, #FFFFFF 33%, #F7F7F7 100%);
    border-top: 0.4rem solid #FF3377;
    border-bottom: 0.1rem solid #f0f0f0;

    h1 {
      padding: 1.4rem;
      font-size: 2.8rem;
      font-family: 'Lato Bold', sans-serif;
      line-height: 1;
      text-align: center;
      text-transform: uppercase;

      a {
        color: #595959;
        text-decoration: none;
      }
    }

    nav {
      ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
      }

      li {
        margin: 0.2rem 0.4rem;

        a {
          display: block;
          padding: 0.8rem 1.2rem;
          text-decoration: none;
          color: #737373;
        }
      }
    }
  }

  &[data-controller="index"][data-view="index"] > header nav li.about a,
  &[data-controller="case-study"][data-view="case-study"] > header nav li.case-studies a,
  &[data-controller="case-studies"][data-view="case-studies"] > header nav li.case-studies a {
    color: #4d4d4d;
    background: #e6e6e6;
    border-radius: 0.6rem;
  }
}

// Main content
main {
  flex: 1 1 auto;
  color: #333333;

  > * {
    margin: 0 1rem;
    overflow-x: hidden;
  }
}

// Mask for zoomed images
#mask {
  position: fixed;
  width: 0;
  height: 0;
  overflow: hidden;
  top: 0;
  opacity: 0;
  transition: opacity 0.2s;
  background: hsla(0,0%,0%,0.95);
  // Use a slightly more opaque background if backdrop-filter isn't supported
  background-color: hsla(0,0%,0%,0.9);
  @supports(backdrop-filter: saturate(180%) blur(0.5rem)) {
    background-color: hsla(0,0%,0%,0.8);
    backdrop-filter: saturate(180%) blur(0.5rem);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &.enabled {
    width: 100%;
    height: 100%;
    padding-top: 6rem;
    padding-bottom: 5rem;
    z-index: 1000;
    opacity: 1;
  }

  &.closing {
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.2s;
  }
}

// Open image in new tab
#mask-open-tab {
  width: 2rem;
  height: 2rem;
  padding-left: 2rem;
  position: absolute;
  top: 2rem;
  right: 6rem;
  background: url('/images/icons/open-new-tab.png') center no-repeat;
  background-size: contain;
  overflow: hidden;
}

// Close mask button
#mask-close {
  width: 2rem;
  height: 2rem;
  padding-left: 2rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  background: url('/images/icons/close.png') center no-repeat;
  background-size: contain;
  overflow: hidden;
}

a.new-tab {
  display: block;
  position: relative;

  &::after {
    content: '';
    display: block;
    width: 4rem;
    height: 4rem;
    position: absolute;
    top: calc(50% - 2rem);
    left: calc(50% - 2rem);
    background: url('/images/icons/zoom.png') center no-repeat;
    background-size: contain;
    opacity: 0;
    transition: opacity 0.2s;
  }

  img {
    transition: filter 0.2s;
  }

  &:hover {
    &::after {
      opacity: 1;
    }
    img {
      filter: brightness(50%);
    }
  }
}

footer {
  padding: 2rem;
  color: #808080;
  text-align: center;

  p.quote {
    margin: 4rem auto;
    font-family: 'Lato Italic', sans-serif;
  }
  
  p.copyright {
    margin-top: 4rem;
    font-size: 1.4rem;
  }
}

// 768px and up
@media only screen and (min-width: 768px) {
  body {
    font-size: 1.8rem;

    > header nav li a:hover,
    &[data-controller="index"][data-view="index"] > header nav li.about a,
    &[data-controller="case-study"][data-view="case-study"] > header nav li.case-studies a,
    &[data-controller="case-studies"][data-view="case-studies"] > header nav li.case-studies a {
      position: relative;
      color: #737373;
      background: none;
  
      &::after {
        content: '';
        display: block;
        width: calc(100% - 3rem);
        height: 0.3rem;
        position: absolute;
        bottom: 0;
        left: 1.5rem;
        background: #999999;
      }
    }

    > header nav li a:hover::after {
      background: #ff3377 !important;
    }
  }

  // Main header
  body > header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 calc((100% - 140rem) / 2);
    font-size: 1.6rem;

    h1 {
      margin: 0 3rem;
      padding: 1rem;
      font-size: 1.8rem;
    }

    nav {
      ul {
        margin-right: 3rem;

        li {
          margin: 0;

          a {
            padding: 2rem 1.5rem 1.9rem;
          }
        }
      }
    }
  }

  main {
    padding: 0 2rem 4rem;
  }

  footer {
    p.quote {
      max-width: 70rem;
    }
  }

  html.js {
    body {
      > header {
        position: fixed;
        z-index: 500;
        transition: margin 0.3s;
      }
      &.hidden-header > header {
        margin-top: -6rem; // Header height
      }
    }

    main {
      padding-top: 6rem !important;
    }
  }
}

// 1024px and up
@media only screen and (min-width: 1024px) {
  body > header {
    padding: 0 calc((100% - 104rem) / 2);
  }

  h2 {
    margin: 8rem 0 4rem;
  }

  h3 {
    margin-top: 4rem;
  }

  main {
    width: 96rem;
    margin: auto;
    padding-left: 0;
    padding-right: 0;
  }
}